@font-face {
  font-family : '손글씨';
  src : url(/나눔손글씨\ 무진장체.ttf);
}

a:link {color:black;}

a:visited {color: black;}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.main-section {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.row {
  display: flex;
  flex-direction: row;
}

.profile-picture {
  width: 300px;
  height: 300px;
  border-radius: 100%;
  overflow: hidden;
}

.profile-picture img {
  background-image: url('../kakaprobg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}

.profile-content {
  margin: 20px;
  font-family : '손글씨';
  font-size: 2rem;
  font-weight: bold;
  transition: 2s;
}

.portfolio-picture {
  width: 900px;
  height: 500px;
  overflow: hidden;
}

.portfolio-picture img {
  background-image: url('../aboutmepic.PNG');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}

.title {
  position: absolute;
  top: 30px;
  left: 50px;
  font-size: 4rem;
  font-family: 손글씨;
  font-weight: bold;
}

.portfolio-content {
  margin: 20px;
  font-family : '손글씨';
  font-size: 2rem;
  font-weight: bold;
}

.contact-content {
  margin: 20px;
  font-family : '손글씨';
  font-size: 2rem;
  font-weight: bold;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
